import React, { Component } from 'react';
import './home.css';
import HomeHeader from './HomeHeader.js';
import HomeFooter from './HomeFooter.js';
var $ = require('jquery');

class Content extends Component {
  render() {
    return (
      <section className="home">
        <HomeHeader />
        <section className="content-container-box">
            <section className="content-header-box">
                <p></p>
                <section className="content-header-line">
                </section>
            </section> 
            <section className="content-image1-box">
                <section className="content-image1-left-box">
                    <p className="title"></p>
                    <p className="content">VR</p> 
                </section>
                <section className="content-image1-right-box">
                    <img src="http://download.duckr.cn/vrschool/content/image_1.png" />
                    <section className="content-view-video">
                        <img src="http://download.duckr.cn/vrschool/content/view_vr_video.jpeg" />
                    </section>
                </section>
            </section>
            <section className="content-image1-box">
                <section className="content-image1-right-box">
                    <img src="http://download.duckr.cn/vrschool/content/imafe_2.png" />
                    <section className="content-view-video">
                        <img src="http://download.duckr.cn/vrschool/content/view_vr_video.jpeg" />
                    </section>
                </section>
                <section className="content-image1-left-box">
                    <p className="title"></p>
                    <p className="content">VR</p> 
                </section>
            </section>
            <section className="content-image1-box">
                <section className="content-image1-left-box">
                    <p className="title"></p>
                    <p className="content"></p> 
                </section>
                <section className="content-image1-right-box">
                    <img src="http://download.duckr.cn/vrschool/content/image_3.png" />
                    <section className="content-view-video">
                        <img src="http://download.duckr.cn/vrschool/content/view_vr_video.jpeg" />
                    </section>
                </section>
            </section>
            <section className="content-bottom-line-header">
            </section>
            <section className="content-bottom-line">
            </section>
        </section>
        <HomeFooter />
      </section>
    );
  }
}

export default Content;



